import * as React from 'react'

interface IconProps {
    src: string,
    bgColor?: string
}
export default class Icon extends React.Component<IconProps, {}> {
  render() {
    return (
        <span className="zd--icon" style={{backgroundColor: this.props.bgColor || "#fff"}}><img src={this.props.src} alt=""/></span>
    )
  }
}
